@media (min-width: 992px) and (max-width: 1199px) {


  .App {
    margin: 0;
    padding: 0;
    min-width: 100%;
    min-height: 100%;
  }

  .App header {
    font-size: 6vw;
    letter-spacing: 15px;
    line-height: 1;
    font-family: "Work Sans";
    font-weight: 200;
    color: ghostwhite;
    text-shadow:  -5px 2px 5px black;
  }

  .audioContainer {
    opacity: 0;
    position: absolute;
    z-index: -3000;
  }

  body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
      "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background: linear-gradient(135deg,#6486ba 25%, #87CEEB 70%, #425c84);
    min-height: 100vh;
    min-width: 100vw;
    text-align: center;
    overflow: hidden;
  }

  .body-filter {
    min-height: 100vh;
    position: absolute;
    min-width: 100vw;
    z-index: -2000;
    opacity: 0;
    background: #2ea045;
  }

  .card-close-button {
    position: absolute;
    top: 5%;
    margin: 0 0 0 27.5%;
    width: 18.5%;
    height: 60px;
    font-size: 2.5vw;
    letter-spacing: .15vw;
    font-weight: 300;
    font-family: "Segoe UI", sans-serif;
    padding: 0;
    z-index: 100;
    border-radius: 2.5%;
    border: 7.5px outset;
    border-image: linear-gradient(10deg, #6f7e99 0%, #6185a0 50%, #6582a3 100%);
    border-image-slice: .45;
    background: linear-gradient(35deg,#516c96 5%, #425c84 90%);
  }

  .card-close-button:hover {
    border: 7.5px inset;
  }

  #contactForm .modal-content {
    background: transparent;
  }

  #contactForm .modal-content .contact-form-close-button {
    font-size: 3vw;
    margin: 0px 0 0 92.5%;
    position: absolute;
    text-align: right;
    z-index: 500;
    color: white;
    text-shadow: 0px 0px 10px red;
    opacity: .8;
  }

  #contactForm .modal-content .contact-form-close-button:hover {
    color: red;
    text-shadow: 0px 0px 0px red;
  }

  #contactForm .modal-content .contact-form-header {
    z-index: 400;
  }

  #contactForm .modal-content .contact-form-header .modal-title{
    text-align: right;
    font-size: 2.25vw;
    line-height: 2.25vw;
    margin: 0 auto;
    color: white;
    font-weight: 400;
    letter-spacing: 1.5px;
    text-shadow: 0px 1px 2.5px black;
  }

  #contactForm .modal-content .modal-body {
    z-index: 300;
  }

  #contactForm .modal-content .modal-body form .contact-form-col .form-group .form-control{
    background: transparent;
    border: 5px inset white;
    color: white;
    font-size: 1.2vw;
    letter-spacing: .9px;
    text-align: center;
    text-shadow: 0px 1px 2.5px black;
  }

  #contactForm .modal-content .modal-body form .contact-form-col .form-group .form-control:focus {
    background: darkgray;
  }

  #contactForm .modal-content .modal-body form .contact-form-col .form-group input {
    line-height: 1.2vw;
    min-height: 6vh;
  }

  #contactForm .modal-content .modal-body form .contact-form-col .form-group input::placeholder{
    z-index: 500;
    color: white;
    text-shadow: 0px 1px 2.5px black;

  }

  #contactForm .modal-content .modal-body form .contact-form-col .form-group textarea{
    padding: 10px 0;
    min-height: 25vh;
    color: white;
  }

  #contactForm .modal-content .modal-body form .contact-form-col .form-group textarea:focus {
    text-align: left;
    padding: 2px;
  }

  #contactForm .modal-content .modal-body form .contact-form-col .form-group textarea::placeholder {
    color: white;
    text-shadow: 0px 1px 2.5px black;

  }

  .contact-form-filter {
    background: linear-gradient(30deg, #7a8aa8 0%, #6b8ba8 50%, #6582a3 100%);
    position: absolute;
    min-width: 100%;
    min-height: 100%;
    opacity: .9;
    z-index: 200;
  }



  .contact-view{
    background: transparent;
    min-height: 70vh;
    max-height: 70vh;
  }





  .contact-view .container .content{
    min-height: 50vh;
    margin: 0;
    padding: 0;
    background: transparent;
  }





  .contact-view .container .content .right {
    padding: 2.5vh 0 0 0 ;
    max-height: 45vh;
  }

  .contact-view .container .header {
    background: transparent;
    margin: 3vh auto 0 auto;
    max-width: fit-content;
    text-align: center;
    min-height: 12.5vh;
    min-width: 100%;
  }





  .grid-button:hover {
    text-shadow: 0px 0px 2.5px ghostwhite;
    border: 5px inset ghostwhite;
  }

  .grid-filter{
    background: linear-gradient(25deg,#5b7bad 5%, #486693 90%);
    min-width: 100%;
    min-height: 100%;
    position: absolute;
    opacity: 0.75;
    z-index: -100;
    margin: -2.5vh -2.5vw;
  }

  .main-row {
    margin: 0;
    display: contents ;
  }

  .main-row-col {
    float: left;
    padding: 0;
    color: black;
    margin-bottom: 2.5vh;
    box-shadow: 2px 2px 7px 1px #98a9bc;
    border-radius: .75%;
  }

  .main-row-col-filter{
    border: 1px solid white;
    border-radius: .75%;
    margin-bottom: 2.5vh;
    height: 100%;
    opacity: .6;
    height: 13.75vh;
    z-index: -1000;
    background: linear-gradient(-120deg, #7a8aa8 0%, #6b8ba8 50%, #4f657f 100%);
    transition: .5s ease-in-out;
  }

  .main-row-col button {
    width: 100%;
    min-width: 100%;
    height: 13.75vh;
    background: transparent;
    border-radius: 0;
    border: 7.5px outset;
    border-image: linear-gradient(10deg, #6f7e99 0%, #6185a0 50%, #6582a3 100%);
    border-image-slice: 1;
  }

  .main-row-col button:hover {
    background: transparent;
    border: 7.5px outset;
    border-image: linear-gradient(10deg, #6f7e99 0%, #6185a0 50%, #6582a3 100%);
    border-image-slice: 1;
  }

  .main-row-col:hover + .main-row-col-filter {
    opacity: .9;
  }


  .project-card .card-body {
    overflow-y: scroll;
  }

  .project-card .card-body .card-text {
    text-align: center;
  }

  .root {
    min-height: 100vh;
  }

  .section-content {
    padding: 0 20px;
  }





  .section-tabs{
    border: none;
    text-align: right;
    display: inline-block;
    margin: 0 0 7.5px 0;
  }




  .section-tabs > .nav-item:hover > a {
    border: none;
    padding: 0.5rem 1rem;
    margin: 1px;
    cursor: pointer;
  }


  .skill-button-filter {
    position: absolute;
  }





  .skill-card .card-title {
    color: white;
    font-family: "Work Sans";
    font-kerning: none;
    font-size: 2.75vw;
    font-weight: 300;
    letter-spacing: .2vw;
    line-height: 2.75vw;
    margin: 1.25vh 0 1.75vh 0;
  }



  .skill-grid-row {
    padding: 0 1%;
  }


  .skill-grid-button:hover {
    background: transparent;
    text-shadow: 0px 0px 2.5px ghostwhite;
    border-color: ghostwhite;
    transition: .45s linear;
  }

  .skill-grid-button.btn-secondary:not(:disabled):not(.disabled).active {
    background: transparent;
    border: 10px outset #6582a3;
    letter-spacing: 1px;
    position: absolute;
    margin: 0 0 0 77.5%;
    top: 20px;
    text-shadow: 0px 0px 2.5px ghostwhite;
    transition-delay: .5s;
    transition: 1s ease-in-out;
  }

  .skill-grid-button.hidden {
    opacity: 0;
  }


}
